import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WaitPhoneComponent } from './waitphone.component';

const routes: Routes = [{path: '', component: WaitPhoneComponent}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class WaitPhoneRoutingModule { }
